import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home.vue'

Vue.use(VueRouter)
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err);
}
const routes = [{
		path: '/',
		name: 'Home',
		component: Home, //底部导航栏组件
		redirect: '/sy',
		children: [{
			path: 'sy',
			name: 'sy',
			component: () => import('../views/sy.vue') //首页
		}, {
			path: 'ks',
			name: 'ks',
			component: () => import('../views/ks.vue') //我的考试页面
		}, {
			path: 'lx',
			name: 'lx',
			component: () => import('../views/lx.vue') //我的练习页面
		}, {
			path: 'kc',
			name: 'kc',
			component: () => import('../views/kc.vue') //我的课程页面
		}, {
			path: 'jf',
			name: 'jf',
			component: () => import('../views/jf.vue') //我的积分页面
		}, {
			path: 'cj',
			name: 'cj',
			component: () => import('../views/cj.vue') //考试成绩页面
		}, {
			path: 'ctb',
			name: 'ctb',
			component: () => import('../views/ctb.vue') //错题本页面
		}, {
			path: 'xgmm',
			name: 'xgmm',
			component: () => import('../views/xgmm.vue') //修改密码页面
		}]
	},
	{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue') //登录页面
	}, {
		path: '/jjcg',
		name: 'jjcg',
		component: () => import('../views/jjcg.vue') //交卷成功
	}, {
		path: '/ksguodu',
		name: 'ksguodu',
		component: () => import('../views/ksguodu.vue') //考试过渡页面
	}, {
		path: '/ksshiti',
		name: 'ksshiti',
		component: () => import('../views/ksshiti.vue') //考试试题页面
	}, {
		path: '/ypt',
		name: 'ypt',
		component: () => import('../views/ypt.vue') //课程云平台页面
	}, {
		path: '/ksks',
		name: 'ksks',
		component: () => import('../views/ksks.vue') //练习过渡页面
	}, {
		path: '/ksnr',
		name: 'ksnr',
		component: () => import('../views/ksnr.vue') //考试内容页面
	}, {
		path: '/kcnr',
		name: 'kcnr',
		component: () => import('../views/kcnr.vue') //课程内容页面
	}, {
		path: '/kcnr1',
		name: 'kcnr1',
		component: () => import('../views/kcnr1.vue') //考试内容页面
	}, {
		path: '/tongji',
		name: 'tongji',
		component: () => import('../views/tongji.vue') //统计页面
	}
]

const router = new VueRouter({
	mode: 'hash',
	base: process.env.BASE_URL,
	routes
})

export default router
